h1{
  font-family: furtura;
  text-align: center;
  display: block;
  color: #f2f2f2;
}
.container{
  display: flex;
  justify-content: center;
  max-width: 500px;
  margin: 0 auto;
}
input{
  height: 50px;
  border-radius: 20px;
  border: none;
  background-color: #f2f2f2;
  font-size: 1.8em;
  transition: 0.2s;
  padding-left: 20px;
  opacity: 0.9;
}
input:focus{
  outline: none;
}
.plus{
  border: none;
  margin-left: 10px;
  border-radius: 90px;
  color: #353535;
  width: 70px;
  transition: 0.5s;
  font-size: 2em;
  background-color: #f2f2f2;
  opacity: 0.9;
}
.plus:active{
  background-color: whitesmoke;
  border-radius: 10px;
}
.listContainer{
  display: flex;
  flex-direction: column;
  max-width: 500px;
  padding: 10px 0;
  margin: 30px auto;
  background-color: #f2f2f2;
  border-radius: 20px;
  min-height: 250px;
  opacity: 0.9;
}
p{
  font-size: 1.8em;
  background-color: #F6F1E9;
  list-style: none;
  padding-left: 10px;
  padding-right: 5px;
  padding-top: 3px;
  padding-bottom: 3px;
  border-radius: 20px;
  margin-top: 0px;
  margin-bottom: 5px;
  margin-left: 10px;
  margin-right: 10px;
  white-space: initial;
  overflow:visible;
  word-wrap: break-word;
  background-color: #FFBB33;
}
input::placeholder{
  font-style: italic;
}
.complete{
  text-decoration: line-through;
}
.clear{
  display: flex;
  justify-content: center;
}
.clearButton{
  border: none;
  margin-left: 10px;
  border-radius: 10px;
  padding: 0.3em;
  transition: 0.5s;
  font-size: 1.8em;
  color: #353535;
  background-color: #f2f2f2;
  opacity: 0.9;
}
@media only screen and (max-width: 767px){
  .container{
    width: 70vw;
    margin-left: 60px;
  }
  input{
    font-size: 30px;
    max-width: 73vw;
  }
  .listContainer{
    margin: 30px 20px;
  }
}



